<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="add">新 增</el-button>
            <!-- <el-button type="default" @click="down">导 出</el-button> -->
        </div>
    </div>
</template>

<script>
    import SearchInputs from "../components/SearchInputs.vue";
    import {
        getBasicLineList,
    } from "@/api/archives.js";
    export default {
        extends: SearchInputs,
        props: {},
        data() {
            return {
                //表单生成规则
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '70px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                rule: [{
                    type: 'input',
                    field: "gcAcc",
                    title: '户号：',
                    value: '',
                    props: {
                        placeholder: '请输入户号',
                    },
                    col: {
                        span: 6,
                    }
                }, {
                    type: 'input',
                    field: "payer",
                    title: '缴费主体：',
                    value: '',
                    props: {
                        placeholder: '请输入缴费主体',
                    },
                    col: {
                        span: 6,
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'glCode',
                    title: '线路：',
                    props: {
                        placeholder: '请输入线路名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false,
                            }
                            if (keyWord !== '') {
                                opt.name = keyWord;
                            }
                            return getBasicLineList(opt).then(res => {
                                if (res.rows) {
                                    const list = res.rows.map(item => ({
                                        ...item,
                                        value: item.name
                                    }))
                                    return list;
                                } else {
                                    return [];
                                }
                            })
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 6
                    }
                }]
            }
        },
        mounted() {

        },
        methods: {

            search() {
                const formData = this.formObj.formData();
                const opt = {
                    ...formData,
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            },
            add() {
                this.$emit('add')
            }
        }
    }
</script>
<style lang="scss">
    .search {
        padding: 24px 0 0px 0;

        .el-form-item {
            margin-bottom: 0.16rem;
        }

        .li {
            display: flex;
            margin-bottom: 16px;
        }
    }
</style>