<!--户号管理-->
<template>
    <BoxTitle>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!--弹窗编辑或新增-->
        <el-dialog :title="currEditRow=='new'?'新增户号':'编辑户号'" custom-class="cusDialog search"
            :visible.sync="this.dialogShow" width="5rem" :before-close="handleClose">
            <form-create v-model="formObj" :rule="rule" :option="options" @main-gc-acc-select="handleSelectMainGc"
                @gl-code-select="handleSelectGl" @company-select="handleSelectCompany">
            </form-create>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">确 定</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
    </BoxTitle>
</template>
<script>
    //组件
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getGcAccQueryByPage,
        postGcAccEdit,
        postGcAccAdd,
        getBasicEleKcList,
        getBasicLineList,
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    export default {
        extends: Index, //继承通用index
        name: 'gcAccount',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                rule: [{
                    type: 'input',
                    field: "gcAccount",
                    title: '户号：',
                    value: '',
                    props: {
                        placeholder: '请输入户号',
                    },
                    on: {
                        change: (value) => {
                            console.log(value);
                            if (this.accountSelf) {
                                this.formObj.form.mainGcAcc = value;
                                this.formObj.exec('mainGcAcc', 'setLabel', value);
                            }
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请输入户号',
                        trigger: 'change'
                    }],
                    col: {
                        span: 24,
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'mainGcAcc',
                    title: '主户号：',
                    value: '',
                    props: {
                        placeholder: '请选择户号或输入主户搜索',
                        fieldValue: 'gcAccount',
                        fieldLabel: 'gcAccount',
                        value: '0',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 10,
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.gcAcc = keyWord;
                            } else {
                                this.formObj.bind().mainGcAcc = '';
                            }
                            return getGcAccQueryByPage(opt).then(res => {
                                if (res.rows) {
                                    let gcAccount = this.formObj.getValue('gcAccount');
                                    let list = res.rows.map(item => ({
                                        ...item,
                                        value: `${item.gcAccount} ${item.glName ? `【${item.glName}】`:'' }`
                                    }));
                                    // 编辑时过滤自己
                                    if (this.currEditRow !== 'new') {
                                        list = list.filter(item => item.gcAccount != this
                                            .currEditRow.gcAccount);
                                    }
                                    return gcAccount ? [{
                                        gcAccount,
                                        value: `${gcAccount}【自己】`
                                    }].concat(list) : list;
                                } else {
                                    return [];
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请输入主户号',
                        trigger: 'change'
                    }],
                    emit: ['select'],
                    col: {
                        span: 24
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'company',
                    title: '供电公司：',
                    props: {
                        placeholder: '请输入公司名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false,
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            } else {
                                this.formObj.bind().company = '';
                            }
                            return getBasicEleKcList(opt).then(res => {
                                if (res.rows) {
                                    const list = res.rows.map(item => ({
                                        ...item,
                                        value: item.name
                                    }))
                                    if (!list.length) { //没有结果 清空上一次选择的
                                        this.formObj.bind().company = '';
                                    }
                                    return list;
                                } else {
                                    return [];
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    col: {
                        span: 24
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'glCode',
                    title: '线路：',
                    props: {
                        placeholder: '请输入线路名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false,
                            }
                            const {
                                company
                            } = this.formObj.formData();
                            if (keyWord !== '') {
                                opt.name = keyWord;
                            } else {
                                this.formObj.bind().glCode = '';
                            }
                            if (company !== '') {
                                opt.gCompanyCode = company;
                            }
                            return getBasicLineList(opt).then(res => {
                                if (res.rows) {
                                    const list = res.rows.map(item => ({
                                        ...item,
                                        value: item.name
                                    }))
                                    if (!list.length) { //线路没有结果 清空上一次选择的线路
                                        this.formObj.bind().glCode = '';
                                    }
                                    return list;
                                } else {
                                    return [];
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    col: {
                        span: 24
                    }
                }, {
                    type: 'input',
                    field: "payer",
                    title: '缴费主体：',
                    value: '',
                    props: {
                        placeholder: '请输入缴费主体',
                    },
                    validate: [{
                        required: true,
                        message: '请输入缴费主体',
                        trigger: 'change'
                    }],
                    col: {
                        span: 24,
                    }
                }, {
                    type: 'input',
                    field: "remark",
                    title: '备注：',
                    value: '',
                    props: {
                        placeholder: '请输入备注',
                    },
                    col: {
                        span: 24,
                    }
                }],
                glName: '', //线路名称
                companyName: '', //公司名称
            }
        },
        methods: {
            //新增户号
            addHand() {
                if (this.currEditRow === 'new') {
                    this.dialogShow = true;
                    return;
                }
                this.currEditRow = 'new'; // 记录当前更改的数据
                this.dialogShow = true;
                this.$nextTick(() => {
                    this.formObj.resetFields();
                    setTimeout(() => {
                        this.formObj.clearValidateState();
                    }, 0);
                    this.formObj.disabled(false, 'gcAccount')
                    this.formObj.exec('mainGcAcc', 'setLabel', '')

                })
            },
            //操作按钮点击事件 目前只有编辑
            btnClick(btnName, row) {
                this.currEditRow = row; // 记录当前更改的数据
                this.dialogShow = true;
                this.$nextTick(() => {
                    row.company && this.getCompanyCodeByName(row.company); //根据名称获取公司code
                    this.formObj.resetFields();
                    const data = this.formObj.form;
                    for (let key in row) {
                        data[key] = row[key];
                    }
                    this.formObj.disabled(true, 'gcAccount');
                    this.formObj.exec('mainGcAcc', 'setLabel', row.mainGcAcc);
                    this.formObj.exec('glCode', 'setLabel', row.glName);
                    this.formObj.exec('company', 'setLabel', row.company);
                    this.companyName = row.company;
                    this.glName = row.glName;
                })
            },
            //根据名称获取公司code,并赋值给表单
            getCompanyCodeByName(name) {
                getBasicEleKcList({
                    keyWord: name,
                    _loading: false,
                }).then(res => {
                    if (res.rows && res.rows.length) {
                        this.formObj.form.company = res.rows[0].code;
                    }
                })
            },
            //默认获取表格数据和搜索数据
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getGcAccQueryByPage({
                    ...this.page,
                    ...this.search
                }).then(res => {
                    this.tableData = res.rows;
                    this.total = res.total;
                })
            },
            //表单提交
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        this.loading = true;
                        if (!data.company) this.companyName = '';
                        if (!data.glCode) this.glName = '';
                        if (this.currEditRow !== 'new') { //编辑
                            postGcAccEdit({
                                ...this.currEditRow,
                                ...data,
                                company: this.companyName,
                                glName: this.glName,
                            }).then(() => {
                                this.reset();
                                this.notice()
                            }).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        } else { //新增
                            postGcAccAdd({
                                ...data,
                                company: this.companyName,
                                glName: this.glName,
                            }).then(() => {
                                this.reset();
                                this.notice()
                            }).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        }
                    }
                })
            },
            //重置表单
            reset() {
                this.formObj.resetFields();
                this.glName = '';
                this.companyName = '';
                this.formObj.exec('mainGcAcc', 'clear')
                this.formObj.exec('company', 'clear')
                this.formObj.exec('glCode', 'clear')
                setTimeout(() => {
                    this.formObj.clearValidateState();
                }, 0);

            },
            //选择供电公司 线路清空
            handleSelectCompany(data, item) {
                this.companyName = item.name; //保存线路名称
                this.formObj.exec('glCode', 'clear')
                this.glName = '';
            },
            //主户号
            handleSelectMainGc(data, item) {
                //选自己的户号作为主户号 记录
                this.accountSelf = item.value.indexOf('自己') != -1;

                this.formObj.validateField('mainGcAcc', () => {})
            },
            //选择线路
            handleSelectGl(data, item) {
                this.glName = item.name; //保存线路名称
            },
        },
        activated: function () {
            this._getData();
        },
        deactivated: function () {
            // 移除任何你不想保留的数据
            this.tableData = [];
        }
    }
</script>
<style scoped lang="scss">
    /deep/ .el-form-item__content .is-disabled {
        background-color: rgba(46, 74, 247, 0.03);
        color: #999;
    }
</style>